<script setup lang="ts">
import { computed } from "vue";
import { getExternalComponent } from "@actindo/core-ui";
import type { IDataTypeInstance } from "@/services/api/DataHub/Attributes";
import type { IDataTypesStringValueConfiguration } from "@/components/dataTypes/StringValue/models";
import { DATAHUB_COMPONENT_TYPE } from "./constants";
import { MOCKED_CONFIGS } from "@/MOCKS";

const props = defineProps<{
  dataTypeInstance: IDataTypeInstance<IDataTypesStringValueConfiguration>;
  type: DATAHUB_COMPONENT_TYPE;
}>();

const value = defineModel<IDataTypesStringValueConfiguration>();

const RequestedComponent = computed(() => {
  if (MOCKED_CONFIGS[props.dataTypeInstance?.id]) {
    // todo REMOVE MOCKS
    // todo DataTypes\IntegerValue.php getVueComponents doesn't work
    switch (props.type) {
      case DATAHUB_COMPONENT_TYPE.CONFIG:
        return MOCKED_CONFIGS[props.dataTypeInstance?.id].configComponent;
    }
  }

  if (!props.dataTypeInstance?.vueComponents) {
    return null;
  }

  switch (props.type) {
    case DATAHUB_COMPONENT_TYPE.CONFIG:
      return getExternalComponent({
        name: props.dataTypeInstance.vueComponents.configurationComponent,
        url: props.dataTypeInstance.vueComponents.configurationImportPath,
      });

    case DATAHUB_COMPONENT_TYPE.VALUE:
      return getExternalComponent({
        name: props.dataTypeInstance.vueComponents.editComponent,
        url: props.dataTypeInstance.vueComponents.editImportPath,
      });

    case DATAHUB_COMPONENT_TYPE.RENDER:
    default:
      return getExternalComponent({
        name: props.dataTypeInstance.vueComponents.renderComponent,
        url: props.dataTypeInstance.vueComponents.renderImportPath,
      });
  }
});
</script>

<template>
  <RequestedComponent
    v-if="RequestedComponent"
    v-model="value"
    :data-type-instance="dataTypeInstance"
  />
</template>
